<template>
  <transition name="modalAnimation">
    <div v-if="showModal" class="lunaModal" :class="{ zIndexHide: !showModal }" :style="wrapStyles">
      <transition name="maskAnimation">
        <div
          v-show="showModal"
          v-if="showMask"
          class="modalMask"
          :class="[
            {
              darkMask: maskStyle === 'dark',
              lightMask: maskStyle === 'light'
            }
          ]"
          @click="handleMask()"
        ></div>
      </transition>
      <div
        class="modalContainer"
        :style="[styles, modalWidth, modalRadius, modalShadow, modalBackground, modalColor]"
        :class="[
          className,
          {
            errorModal: type === 'error',
            successModal: type === 'success',
            warningModal: type === 'warning',
            primaryModal: type === 'primary',
            alertModal: type === 'alert',
            '': type === 'normal'
          },
          {
            darkShadow: shadowStyle === 'dark',
            lightShadow: shadowStyle === 'light'
          },
          {
            squareCorner: corner === 'square',
            smallCorner: corner === 'small',
            filletCorner: corner === 'fillet',
            largeCorner: corner === 'large',
            fullCorner: corner === 'full'
          },
          { lightModal: theme === 'light', darkModal: theme === 'dark' }
        ]"
      >
        <div v-if="showHead" class="modalHeader">
          <slot name="header">
            <i v-if="fontIcon !== ''" :style="[iconFontSizeCount, iconColorCount]" :class="fontIcon"></i>
            <img v-if="imageIcon !== ''" :src="imageIcon" />
            <p :style="titleColorCount">{{ title }}</p>
          </slot>
          <i class="iconfont icon-cancel closeIcon" @click="close"></i>
        </div>
        <div class="modalBody" :style="[modalHeight, modalMaxHeight, modalScrollable, modalBodyCount]">
          <slot></slot>
        </div>
        <div v-if="footerShow" class="modalFooter">
          <slot name="footer">
            <button v-ripple class="normalButton" @click="close">
              {{ $t('lang.todoPanel.button.cancel') }}
            </button>
            <button v-ripple class="primaryButton" @click="confirm">
              {{ $t('lang.todoPanel.button.confirm') }}
            </button>
          </slot>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  props: {
    // 
    value: {
      type: Boolean,
      default: false
    },
    icon: {
      type: String,
      default: ''
    },
    iconColor: {
      type: [Number, String],
      default: ''
    },
    iconFontSize: {
      type: [Number, String],
      default: 16
    },
    // 
    showMask: {
      type: Boolean,
      default: true
    },
    // 
    maskClosable: {
      type: Boolean,
      default: true
    },
    // title
    title: {
      type: [Number, String]
    },
    //    Esc 
    closeable: {
      type: Boolean,
      default: true
    },
    // 
    className: {
      type: String,
      default: ''
    },
    theme: {
      type: String,
      default: 'light'
    },
    type: {
      type: String,
      default: 'normal'
    },
    // 
    styles: {
      type: Object,
      default() {
        return {}
      }
    },
    // z-index
    zIndex: {
      type: Number,
      default: 250
    },
    // 
    footerShow: {
      type: Boolean,
      default: true
    },
    // body
    scrollable: {
      type: Boolean,
      default: false
    },
    //   container
    width: {
      type: [Number, String],
      default: 600
    },
    //   body
    height: {
      type: [Number, String],
      default: 'auto'
    },
    //   body
    maxHeight: {
      type: [Number, String],
      default: 'auto'
    },
    radius: {
      type: Number,
      default: -1
    },
    maskStyle: {
      type: String,
      default: 'dark'
    },
    shadow: {
      type: Boolean,
      default: true
    },
    shadowStyle: {
      type: String,
      default: 'light'
    },
    corner: {
      type: String,
      default: 'large'
    },
    color: {
      type: [Number, String],
      default: ''
    },
    titleColor: {
      type: [Number, String],
      default: ''
    },
    background: {
      type: [Number, String],
      default: 'none'
    }
  },
  data() {
    return {
      showModal: this.value,
      showHead: true,
      colorTheme: this.type,
      fontIcon: '',
      imageIcon: ''
    }
  },
  mounted() {
    if (this.$slots.header === undefined && !this.title) {
      this.showHead = false
    }
    const icon = this.icon
    // 
    const re = /(\\+)/g
    const filename = icon.replace(re, '#')
    // 
    const fileArray = filename.split('#')
    // 
    const fileName = fileArray[fileArray.length - 1]
    // 
    const suffixName = fileName.split('.')
    // 
    const last = suffixName[suffixName.length - 1]
    // 
    const tp = 'jpg,svg,png,JPG,SVG,PNG'
    // 
    const rs = tp.indexOf(last)
    // 0
    if (rs >= 0) {
      this.imageIcon = this.icon
    } else {
      this.fontIcon = this.icon
    }
  },
  methods: {
    //   @on-cancel()
    close() {
      this.showModal = false
      this.$emit('input', false)
      this.$emit('on-cancel')
    },
    confirm() {
      this.$emit('on-ok')
      this.close()
    },
    // 
    handleMask() {
      if (this.closeable && this.showMask) {
        this.close()
      }
    },
    // 
    cancel() {
      this.close()
    }
  },
  computed: {
    modalBodyCount() {
      if (!this.showHead) {
        return { paddingTop: '48px' }
      }
      return {}
    },
    wrapStyles() {
      return {
        zIndex: this.zIndex
      }
    },
    modalWidth() {
      if (typeof this.width !== 'string') {
        if (this.width <= 100) {
          return {
            width: this.width + '%'
          }
        } else {
          return {
            width: this.width + 'px'
          }
        }
      } else {
        return {
          width: this.width
        }
      }
    },
    modalRadius() {
      if (this.radius !== -1) {
        return {
          borderRadius: this.radius + 'px!important'
        }
      } else {
        return {}
      }
    },
    modalColor() {
      if (this.color !== '') {
        if (typeof this.color === 'string') {
          return { color: this.color + '!important' }
        } else {
          return { color: '#' + this.color + '!important' }
        }
      }
      return {}
    },
    modalShadow() {
      if (!this.shadow) {
        return {
          boxShadow: '0 0 0 rgba(0, 0, 0, 0)!important'
        }
      } else {
        return {}
      }
    },
    modalHeight() {
      if (this.height !== 'auto') {
        return {
          height: this.height + 'px'
        }
      } else {
        return {
          height: this.height
        }
      }
    },
    modalMaxHeight() {
      if (this.maxHeight !== 'auto') {
        return {
          maxHeight: this.maxHeight + 'px'
        }
      } else {
        return {
          maxHeight: this.maxHeight
        }
      }
    },
    modalScrollable() {
      if (this.scrollable && this.maxHeight !== 'auto') {
        return {
          overflow: 'auto'
        }
      } else {
        return {
          overflow: 'hidden'
        }
      }
    },
    modalBackground() {
      if (this.background !== 'none') {
        if (typeof this.background === 'string') {
          return {
            backgroundColor: this.background + '!important'
          }
        } else {
          return {
            backgroundColor: '#' + this.background + '!important'
          }
        }
      }
      return {}
    },
    iconColorCount() {
      if (this.iconColor !== '') {
        if (typeof this.iconColor === 'string') {
          return { color: this.iconColor }
        } else {
          return { color: '#' + this.iconColor }
        }
      }
      return {}
    },
    iconFontSizeCount() {
      if (typeof this.iconFontSize === 'string') {
        return { fontSize: this.iconFontSize }
      } else {
        return { fontSize: this.iconFontSize + 'px' }
      }
    },
    titleColorCount() {
      if (this.titleColor !== '') {
        if (typeof this.titleColor === 'string') {
          return {
            color: this.titleColor + '!important'
          }
        } else {
          return {
            color: '#' + this.titleColor + '!important'
          }
        }
      }
      return {}
    }
  },
  watch: {
    value(val) {
      this.showModal = val
    }
  }
}
</script>

<style lang="scss" scoped></style>
